import {Action} from '@ngrx/store';
import {UserInterface} from '../../core/models/user.interface';

export const USER_LOGIN = 'USER_LOGIN';
export const USER_LOGIN_SUCCESS = 'USER_LOGIN_SUCCESS';
export const USER_LOGIN_FAILED = 'USER_LOGIN_FAILED';
export const USER_SIGNUP = 'USER_SIGNUP';
export const USER_SIGNUP_SUCCESS = 'USER_SIGNUP_SUCCESS';
export const USER_SIGNUP_FAILED = 'USER_SIGNUP_FAILED';
export const USER_LOGOUT = 'USER_LOGOUT';
export const GET_CURRENT_USER = 'GET_CURRENT_USER';
export const SET_CURRENT_USER = 'SET_CURRENT_USER';
export const TOKEN_VERIFIED = 'TOKEN_VERIFIED';
export const TOKEN_VERIFICATION_FAILED = 'TOKEN_VERIFICATION_FAILED';
export const  AUTH_VALIDATE_PHONE = 'AUTH_VALIDATE_PHONE';
export const  AUTH_SET_VALIDATE_PHONE_RESPONSE = 'AUTH_SET_VALIDATE_PHONE_RESPONSE';
export const AUTH_VALIDATE_TOKEN = 'AUTH_VALIDATE_TOKEN';
export const EMPTY = 'EMPTY';

export class UserLoginSuccess implements Action {
    readonly type = USER_LOGIN_SUCCESS;
    constructor(public payload) {}
}

export class UserLoginFailed implements Action {
    readonly type = USER_LOGIN_FAILED;
    constructor(public payload) { }
}

export class UserLogin implements Action {
    readonly type = USER_LOGIN;
    constructor(public payload) { }
}

export  class  TokenVerified implements Action {
    readonly type = TOKEN_VERIFIED;
}

export  class  TokenVerificationFailed implements Action {
    readonly type = TOKEN_VERIFICATION_FAILED;
}

export class UserSignup implements Action {
    readonly type = USER_SIGNUP;
    constructor(public payload) { }
}

export class UserSignupSuccess implements Action {
    readonly type = USER_SIGNUP_SUCCESS;
    constructor(public payload) { }
}

export class UserSignupFailed implements Action {
    readonly type = USER_SIGNUP_FAILED;
    constructor(public payload) { }
}

export class UserLogout implements  Action {
    readonly type = USER_LOGOUT;
}

export class SetCurrentUser implements  Action {
    readonly type = SET_CURRENT_USER;
    constructor(public payload) { }
}

export class GetCurrentUser implements  Action {
    readonly type = GET_CURRENT_USER;
}


export class AuthValidatePhone implements  Action {
    readonly type = AUTH_VALIDATE_PHONE;
    constructor(public payload: UserInterface) { }
}

export class AuthSetValidatePhoneResponse implements  Action {
    readonly type = AUTH_SET_VALIDATE_PHONE_RESPONSE;
    constructor(public payload: UserInterface) { }
}

export class ValidateToken implements  Action {
    readonly type = AUTH_VALIDATE_TOKEN;
    constructor(public payload) { }
}

export class Empty implements Action {
    readonly type = EMPTY;
}
export type AuthActions = UserLogin | UserSignupSuccess | UserSignup | TokenVerified | TokenVerificationFailed | GetCurrentUser
       | AuthSetValidatePhoneResponse | AuthValidatePhone | SetCurrentUser | UserLogout | UserLoginSuccess | UserLoginFailed |
    UserSignupFailed | ValidateToken | Empty;