import {UserInterface} from '../../core/models/user.interface';
import * as AuthAction from './authentication.actions';
import {AppState} from '../app.reducer';


export interface State {
    token: string;
    authenticated: boolean;
    currentUser: UserInterface;
    loading: boolean;
    error: null;
    pvResponse: {
        'status': number,
        'message': string,
        'verificationcode': string,
        'verified': boolean,
        'nextpage': string
    }; // this is the response we receive after validating the user phone number
}

export const initialState: State = {
    token: null,
    authenticated: false,
    error: null,
    currentUser: null,
    loading: false,
    pvResponse: null
};


export  function authReducer(state = initialState, action: AuthAction.AuthActions) {
    switch (action.type) {
        case AuthAction.USER_LOGIN_SUCCESS:
            return {
                ...state,
                authenticated: true,

            };
        case AuthAction.USER_LOGIN_FAILED:
            return {
                ...state,
                authenticated: false,

            };
        case AuthAction.USER_SIGNUP_FAILED:
            return {
                ...state,
                authenticated: false,
                error: action.payload

            };
        // case AuthAction.SEND_PASSWORD_RESET_FAILED:
        //     return {
        //         ...state,
        //         error: action.payload
        //
        //     };
        case AuthAction.TOKEN_VERIFIED:
            return {
                ...state,
                isTokenVerified: true,
            };

        case AuthAction.TOKEN_VERIFICATION_FAILED:
            return {
                ...state,
                isTokenVerified: false,
            };

        case AuthAction.USER_LOGOUT:
            return {...initialState};
        case AuthAction.SET_CURRENT_USER:
            return {
                ...state,
                currentUser: action.payload.user,
                token: action.payload.token,
            };
        // case AuthAction.EDIT_CURRENT_USER:
        //     return {
        //         ...state,
        //         currentUser:action.payload
        //     };
        // case AuthAction.SHOW_GLOBAL_LOADING:
        //     return{
        //         ...state,
        //         loading: true
        //     };
        // case AuthAction.HIDE_GLOBAL_LOADING:
        //     return{
        //         ...state,
        //         loading: false
        //     };
        default:
            return state;
    }
}

export const getCurrentUser = (state: State) => state.currentUser;
export const getAuthenticated = (state: State) => state.authenticated;
export const getLoading = (state: State) => state.loading;
export const getError = (state: State) => state.error;
