import {
  HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest,
  HttpResponse
} from '@angular/common/http';
import {Injectable, Injector} from '@angular/core';
import {Observable, pipe, throwError} from 'rxjs';
import {tap } from 'rxjs/operators';
import {Router} from '@angular/router';
import {Store} from '@ngrx/store';
import {AppState} from '../../state/app.reducer';
import {UtilityService} from '../services/utility.service';
import {AuthService} from '../services/auth.service';
import {ValidateToken} from "../../state/authentication/authentication.actions";


@Injectable()
export class TokenInterceptor implements HttpInterceptor {

  private auth;

  private store;

  constructor(private injector: Injector, auth: AuthService, store: Store<AppState>) {
    this.auth = auth;
    this.store = store;
  }
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const token = this.auth.getToken();
    let cloneReq: HttpRequest<any>;

    const exclude = /(login|register)/gi;


    if (token && req.url.search(exclude) === -1) {
        console.log("worked")
        cloneReq = req.clone({setHeaders : {
                 Authorization: `bearer ${token}`,
                'Content-Type': 'application/json',
            }});
        return next.handle(cloneReq)
            .pipe(tap((event: HttpEvent<any>) => {
                    if (event instanceof HttpResponse) {
                        // do stuff with response if you want
                    }
                },
                (err: any) => {
                    if (err instanceof HttpErrorResponse) {
                        // || err.status===400
                        if (err.status === 401) {
                            this.auth.refreshToken();
                            this.store.dispatch(new ValidateToken(token));
                        } else if (err.status === 403) {
                          //TODO: dispatch error code in ui
                        }
                    } else {
                        return throwError(cloneReq);
                    }
                }
            )
        );
    } else {
      return next.handle(req);
    }
   // return next.handle((req));
  }


}
