import * as fromAuthActions from './authentication.actions';
import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {Action} from '@ngrx/store';
import {Empty, SetCurrentUser, UserLogin, UserLoginFailed, UserLoginSuccess, UserLogout, UserSignup, UserSignupFailed, UserSignupSuccess
        ,TokenVerified, TokenVerificationFailed}
    from './authentication.actions';
import {catchError, map, mergeMap, switchMap, tap} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {UtilityService} from '../../core/services/utility.service';
import {Router} from '@angular/router';
import {AuthService} from '../../core/services/auth.service';
import {Observable, of} from 'rxjs';

@Injectable()
export class AuthEffects {
    @Effect()
    loginSuccess = this.actions$
        .pipe(
            ofType(fromAuthActions.USER_LOGIN_SUCCESS),
            map((action: fromAuthActions.UserLoginSuccess) => {
                this.authService.setToken(action.payload.token);
                const s = new SetCurrentUser(action.payload.user);
                this.router.navigateByUrl('/');
                return new Empty();
            })
        );
    @Effect()
    singUpSuccess = this.actions$
        .pipe(
            ofType(fromAuthActions.USER_SIGNUP_SUCCESS),
            map((action: fromAuthActions.UserSignupSuccess) => {
                this.authService.setToken(action.payload.token);
                const s = new SetCurrentUser(action.payload.user);
                this.router.navigateByUrl('/');
                return new Empty();

            })
        );
    @Effect()
    login: Observable<Action> = this.actions$
        .pipe(
            ofType(fromAuthActions.USER_LOGIN),
            switchMap((action: fromAuthActions.UserLogin) => {
                console.log('called');
                console.log(action.payload);
                console.log(this.http);
                return this.http.post( '/api/Token', action.payload)
                    .pipe(
                        map((data) => {
                            console.log(data);
                            this.authService.setToken(action.payload.token);
                            return new UserLoginSuccess(data);
                        }),
                        catchError((error) => {
                            // const data = this.utilityService.getError(error);
                            return of(new UserLoginFailed('An error occurred'));
                        })
                    );
            })
        );
    @Effect({ dispatch: false })
    logout: Observable<any> = this.actions$
        .pipe(
            ofType(fromAuthActions.USER_LOGOUT),
            map((action: fromAuthActions.UserLogout) => {
                this.authService.removeToken();
                this.router.navigate(['/login']);
                return new Empty();
            })
        );
    @Effect()
    signUp: Observable<Action> = this.actions$
        .pipe(
            ofType(fromAuthActions.USER_SIGNUP),
            switchMap((action: fromAuthActions.UserSignup) => {
                return this.http.post( '/api/customer/CustomerRegisteration', action.payload)
                    .pipe(
                        map((data) => {
                            return new UserSignupSuccess(data);
                        }),
                        catchError((error) => {
                            return of(new UserSignupFailed('An error occurred'));
                        })
                    );
            })
        );
    @Effect()
    getCurrentUser = this.actions$
        .pipe(
            ofType(fromAuthActions.GET_CURRENT_USER),
            switchMap((action: fromAuthActions.SetCurrentUser) => {
                return this.http.get('/api/accounts/api/v1/get-profile/');
            }),
            map((userData) => {
                return { type: fromAuthActions.SET_CURRENT_USER, payload: userData};
            }),
        );
        @Effect()
     validateToken = this.actions$
        .pipe(
            ofType(fromAuthActions.AUTH_VALIDATE_TOKEN),
            map((action: fromAuthActions.ValidateToken) => {
                const token = this.authService.getToken();
                const data = action.payload;
                var path = window.location.pathname.split("/");

                if(token && data.exclude.includes(data.current)){
                    this.router.navigate(['/']);
                }
                else if(!token){
                    if(data.exclude.includes(path[1])){
                        console.log(path[1]);
                        this.router.navigate(['/'+ path[1]]);
                    }else{
                        this.router.navigate(['/login']);
                    }
                }
                return new Empty();
            })
        );
    constructor(private actions$: Actions,
                private router: Router,
                private authService: AuthService,
                private http: HttpClient) {}
}