
import * as fromAuth from './authentication/authentication.reducers';
import * as fromActivity from './activity/activity.reducers';
import {ActionReducerMap} from '@ngrx/store';

export interface AppState {
    auth: fromAuth.State;
    activity: fromActivity.State

}
export function defaultReducer<T>(state: T) { return state; }

export const initialReducerMap: ActionReducerMap<AppState> = {
    auth: fromAuth.authReducer,
    activity: fromActivity.activityReducer
};
