import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/state/app.reducer';
import { AuthService } from 'src/app/core/services/auth.service';
import {Router} from '@angular/router';
import {ActivityService} from '../../../core/services/activity.service';
import {Observable} from 'rxjs';

@Component({
  selector: 'pro-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  showProfileDrop: boolean = false;
  hideCreateActivity$: Observable<boolean>;
  isAuth;

  constructor(private store: Store<AppState>,
              private router: Router,
              private activityService: ActivityService,
              private auth: AuthService) { }

  ngOnInit() {
    this.isAuth = this.auth.getToken();
    this.hideCreateActivity$ = this.activityService.getShowAddActivity()
  }

  profileSetting(){
    this.showProfileDrop = !this.showProfileDrop;
  }

  createActivity() {
    this.router.navigate(["/activity/create"])
  }


}
