import * as ActivityAction from './activity.actions';
import {AppState} from '../app.reducer';
import {ActivityInterface} from '../../core/models/activity.interface';
import {ActivityTypesInterface} from '../../core/models/activity-types.interface';
import {AddonInterface} from '../../core/models/addon-interface';


export interface State {
    activity: ActivityInterface;
    activities: Array<ActivityInterface>;
    activityTypes: ActivityTypesInterface;
    allAddOns: Array<AddonInterface>;
}

export const initialState: State = {
    activity:  null,
    activities: null,
    allAddOns: null,
    activityTypes: null
};


export  function activityReducer(state = initialState, action: ActivityAction.ActivityActions) {
    switch (action.type) {
        case ActivityAction.SET_ACTIVITY:
            return {
                ...state,
                activity: action.payload,

            };
        case ActivityAction.SET_ACTIVITIES:
            return {
                ...state,
                activities: action.payload,

            };
        case ActivityAction.SET_ACTIVITY_ATTR:
            let activity = {}
            if (state.activity) {
               activity = state.activity;
            }
            activity[action.payload.name] = action.payload.value;
            return {
                ...state,
                activity: activity

            };
        case ActivityAction.SET_ACTIVITY_TYPES:
            return {
                ...state,
                activityTypes: action.payload

            };
        case ActivityAction.SET_ALL_ADD_ONS:
            return {
                ...state,
                allAddOns: action.payload
            }
        default:
            return state;
    }
}

export const getActivity = (state: State) => state.activity;
export const getActivities = (state: State) => state.activities;
export const getTypes = (state: State) => state.activityTypes;
export const getActiviyPhotos = (state: State) => state.activity.activity_Photos;
export const getAllAddOns = (state: State) => state.allAddOns;


