import { Component, OnInit } from '@angular/core';
import { AuthService } from './core/services/auth.service';
import { AuthInterceptor } from './core/interceptors/auth-interceptor';
import { ActivatedRoute } from '@angular/router';
import { LocationStrategy, PathLocationStrategy } from '@angular/common';
import {Router, Route} from '@angular/router';
import { AppState } from './state/app.reducer';
import { Store } from '@ngrx/store';
import { UserLogout, ValidateToken } from './state/authentication/authentication.actions';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent implements OnInit  {

  title = 'backpack';

  inAuth = false;
  authService = null;
  store
  router;

  private exclude = ['login', 'register'];

  constructor( auth: AuthService, router: Router, store: Store<AppState> ) {
      this.authService = auth;
      this.router = router;
      this.store = store;
   }

  ngOnInit() { 

    const path = window.location.pathname.split("/");

    const payload = {
       current: path.shift(),
       exclude: this.exclude
    };
    this.store.dispatch(new ValidateToken(payload));
    const token = this.authService.getToken();

  }

}
