import * as fromActivityAction from './activity.actions';
import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {Action} from '@ngrx/store';
import {catchError, map, mergeMap, switchMap, tap} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {UtilityService} from '../../core/services/utility.service';
import {Router} from '@angular/router';
import {Observable, of} from 'rxjs';
import {Empty} from '../authentication/authentication.actions';

@Injectable()
export class ActivityEffects {

    @Effect()
    getActivityTypes: Observable<Action> = this.actions$
        .pipe(
            ofType(fromActivityAction.GET_ACTIVITY_TYPES),
            switchMap((action: fromActivityAction.GetActivityTypes) => {
                return this.http.get( '/Api/Activity/GetActivityTypes')
                    .pipe(
                        map((data) => {
                            return new fromActivityAction.SetActivityTypes(data)
                        }),
                        catchError((error) => {
                            return of(new Empty())
                        })
                    );
            })
        );

    @Effect()
    getActivity = this.actions$
        .pipe(
            ofType(fromActivityAction.GET_ACTIVITY),
            switchMap((action: fromActivityAction.GetActivity) => {
                return this.http.get( '/Api/Activity/GetActivityDetails?activityid='+ action.payload)
                    .pipe(
                        map((data) => {
                            return new fromActivityAction.SetActivity(data)
                        }),
                        catchError((error) => {
                            return of(new Empty())
                        })
                    );
            })
        );

    @Effect()
    getAddons = this.actions$
        .pipe(
            ofType(fromActivityAction.GET_ALL_ADD_ONS),
            switchMap((action: fromActivityAction.GetAllAddOns) => {
                return this.http.get( '/api/add_ons/GetAllAdd_Ons')
                    .pipe(
                        map((data) => {
                            return new fromActivityAction.SetAllAddOns(data)
                        }),
                        catchError((error) => {
                            return of(new Empty())
                        })
                    );
            })
        );

    @Effect()
    createAddon = this.actions$
        .pipe(
            ofType(fromActivityAction.CREATE_ADD_ON),
            switchMap((action: fromActivityAction.CreateAddOn) => {
                return this.http.post( '/api/Add_ons/CreateAdd_Ons', action.payload)
                    .pipe(
                        map((data) => {
                            return new fromActivityAction.GetAllAddOns()
                        }),
                        catchError((error) => {
                            return of(new Empty())
                        })
                    );
            })
        );

    @Effect()
    deleteAddon = this.actions$
        .pipe(
            ofType(fromActivityAction.DELETE_ADD_ON),
            switchMap((action: fromActivityAction.DeleteAddOns) => {
                return this.http.get( '/api/add_ons/DeleteAddons?id='+action.payload)
                    .pipe(
                        map((data) => {
                            return new fromActivityAction.GetAllAddOns()
                        }),
                        catchError((error) => {
                            return of(new Empty())
                        })
                    );
            })
        );

    @Effect()
    createActivity: Observable<Action> = this.actions$
        .pipe(
            ofType(fromActivityAction.CREATE_ACTIVITY),
            switchMap((action: fromActivityAction.CreateActivity) => {
                return this.http.post( '/Api/Activity/Create_Activity?activityid=0&mode=1', action.payload)
                    .pipe(
                        map((data) => {
                           return new fromActivityAction.GetActivity(data["activityId"])
                        }),
                        catchError((error) => {
                            // const data = this.utilityService.getError(error);
                            return of(new Empty())
                        })
                    );
            })
        );

    constructor(private actions$: Actions,
                private router: Router,
                private http: HttpClient) {}
}
