import {Component, ElementRef, EventEmitter, NgZone, OnInit, Output, ViewChild} from '@angular/core';
import { FormControl } from '@angular/forms';
declare const google: any;
import { MapsAPILoader } from '@agm/core';

@Component({
    selector: 'location-search-input',
    styles: [`
    agm-map {
      height: 100%;
    }
  `],
    template: `
        <div class="map_input">
            <input
                    type="search"
                    placeholder="Search here or select range from map"
                    autocomplete="off"
                    autocapitalize="off"
                    autofocus
                    [formControl]="searchControl"
                    #search
                    class="input_control" />
            <span class="pin_icon"><i class="material-icons">location_on</i></span>
        </div>
        <div class="map" style="	height: 130px;">
            <agm-map style="height: 100%" [latitude]="" [longitude]="longitude" [zoom]="zoom">
                <agm-marker [latitude]="latitude" [longitude]="longitude"></agm-marker>

            </agm-map>
        </div>
    
  `
})
export class LocationSearchInputComponent implements OnInit {

    public latitude: number;
    @Output("lat") lat: EventEmitter<number> = new EventEmitter<number>();
    @Output("long") long: EventEmitter<number> = new EventEmitter<number>();
    @Output("address") address: EventEmitter<string> = new EventEmitter<string>();
    public longitude: number;
    public searchControl: FormControl;
    public zoom: number;

    @ViewChild("search")
    public searchElementRef: ElementRef;

    constructor(
        private mapsAPILoader: MapsAPILoader,
        private ngZone: NgZone
    ) {}

    ngOnInit() {
        //set google maps defaults
        this.zoom = 4;
        this.latitude = 39.8282;
        this.longitude = -98.5795;

        //create search FormControl
        this.searchControl = new FormControl();

        //set current position
        this.setCurrentPosition();

        //load Places Autocomplete
        this.mapsAPILoader.load().then(() => {
            let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
                types: ["address"]
            });
            autocomplete.addListener("place_changed", () => {
                this.ngZone.run(() => {
                    //get the place result
                    let place = autocomplete.getPlace();

                    //verify result
                    if (place.geometry === undefined || place.geometry === null) {
                        return;
                    }

                    //set latitude, longitude and zoom
                    console.log(place, "place")
                    this.latitude = place.geometry.location.lat();
                    this.longitude = place.geometry.location.lng();
                    this.lat.emit(this.latitude);
                    this.long.emit(this.longitude);
                    this.address.emit(place.formatted_address);
                    this.zoom = 12;
                });
            });
        });
    }

    private setCurrentPosition() {
        if ("geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition((position) => {
                this.latitude = position.coords.latitude;
                this.longitude = position.coords.longitude;
                this.zoom = 12;
            });
        }
    }
}