import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './backpack-pro/pages/login/login.component';
import { RegisterComponent } from './backpack-pro/pages/register/register.component';
import { HomeComponent } from './backpack-pro/pages/home/home.component';

import { UserAccountComponent } from './backpack-pro/pages/user-account/user-account.component';
import { OrgAccountComponent } from './backpack-pro/pages/org-account/org-account.component';
import { MessagingComponent } from './backpack-pro/pages/messaging/messaging.component';
import { BookingReservationComponent } from './backpack-pro/pages/booking-reservation/booking-reservation.component';
import { CalendarComponent } from './backpack-pro/pages/calendar/calendar.component';

const routes = [
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'reset-password', component: RegisterComponent },
  { path: 'user-account', component: UserAccountComponent },
  { path: 'org-account', component: OrgAccountComponent },
  { path: 'activity', loadChildren: './backpack-pro/pages/activity/activity.module#ActivityModule' },
    { path: 'calendar' ,         component: CalendarComponent },
    { path: '', component: HomeComponent },
  { path: '**', redirectTo: '/' }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


