import { Injectable } from '@angular/core';
import {ReplaySubject, Subject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ActivityService {
  showAddActivity = new ReplaySubject<boolean>(1)
  constructor() {
    this.setShowAddActivity(true)
  }

  setShowAddActivity(value){
    this.showAddActivity.next(value)
  }
  getShowAddActivity(){
    return this.showAddActivity
  }
}
