import { Injectable } from '@angular/core';
import  * as Cookie from 'js-cookie';
import { User } from '../models/user';

@Injectable({
  providedIn: 'root',
})
export class AuthService {

  constructor() { }
  getToken() {
        return Cookie.get('token');
  }
  setToken(token) {
        return Cookie.set('token', token, {expires: 7});
  }
  removeToken() {
      Cookie.set('token', '');
  }

  setUser(user: User){
    return Cookie.set('user', JSON.stringify(user), {expires: 7});
  }

  getUser(){
    const user_string = Cookie.get('user');
    return JSON.parse(user_string);
  }

}
