import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {UtilityService} from '../../core/services/utility.service';

// <span style="color:red;" *ngIf="message">{{message}}</span>

@Component({
  selector: 'upload-imge',
  template: `
    <div class="upload-list">
      <div class="uplaod_preview" *ngIf="!getCoverPhoto()">
           <input #file type="file"  *ngIf="allowMultiple" multiple accept='image/*' (change)="preview(file.files)" />
           <input #file type="file"  *ngIf="!allowMultiple"  accept='image/*' (change)="preview(file.files)" />
           <span class="add_icon" ><i class="material-icons">add</i></span>
      </div>
        <div class="uplaod_preview" *ngIf="getCoverPhoto()">
            <img  [src]="coverPhoto?.base64Img" height="100%">
        </div>
        <div class="uplaod_preview" style="margin-right:2px"
             (click)="makeCoverPhoto(image)" *ngFor="let image of uploaders;">
            <img [src]="image.base64Img" height="100%">
        </div>
        
      <div class="uplaod_preview" *ngIf="getCoverPhoto()">
         <input #file type="file"  *ngIf="allowMultiple" multiple accept='image/*' (change)="preview(file.files)" />
         <input #file type="file"  *ngIf="!allowMultiple"  accept='image/*' (change)="preview(file.files)" />
        <span class="add_icon" *ngIf="!imgURL"><i class="material-icons">add</i></span>
      </div>
    </div>
  `,
  styles: [`
    .uplaod_preview {
      background: #fff;
      border: 2px dashed #ccc;
      text-align: center;
      position: relative;
      margin-bottom: 20px;
      width: 110px;
      height: 100px;
      float: left;
      cursor: pointer;
    }
    .uplaod_preview:first-child{
      height: 170px;
      width: 100%;
    }
    .uplaod_preview:first-child .add_icon i{
      font-size: 60px;
    }
    .uplaod_preview:first-child .add_icon {
      position: relative;
      top: calc(50% - 30px);
    }
    input[type="file"] {
      width: 100%;
      height: 100%;
      outline: none !important;
      position: absolute;
      left: 0;
      opacity: 0;
      z-index: 1;
      cursor: pointer;
    }
    .add_icon {
      position: relative;
      top: calc(50% - 15px);
    }
    .add_icon i{
      font-size: 30px;
      color: #FF6B4B;
    }
  `]
})
export class UploadImgeComponent implements OnInit {

  constructor(private utilityService: UtilityService) { }

  ngOnInit() {
  }

  public imagePath;
  coverPhoto;
  imgURL: any;
  @Input("allowMultiple") allowMultiple;
  @Input("uploaders") uploaders = [];
  @Output("uploadedImage") uploadedImage: EventEmitter<any> = new EventEmitter<any>();
  public message: string;

  preview(files) {
    if (files.length === 0)
      return;

    const mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.message = "Only images are supported.";
      return;
    }


    this.imagePath = files;
    if( this.allowMultiple ) {
        for (let file of files) {
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = (event) => {
                // this.uploaders.push(this.utilityService.removeBase64(reader.result));
                this.uploaders.push({base64Img: reader.result, cover_photo: false});
            }
        }
        this.uploadedImage.emit(this.uploaders);


    } else {
        const reader = new FileReader();
        reader.readAsDataURL(files[0]);
        reader.onload = (event) => {
            this.imgURL = reader.result;
            this.uploadedImage.emit(reader.result)
        }
    }

  }
  getCoverPhoto(){
      if (this.uploaders) {
          const cover = this.uploaders.filter( (img) => {
              if (img.cover_photo)
                  return img
          });
          if (cover.length) {
              this.coverPhoto = cover[0];
              return true;
          } else if(this.uploaders.length) {
              this.coverPhoto = this.uploaders[0];
              return true;
          } else {
              return false;
          }
      }

  }
  makeCoverPhoto (image){
        for(const f of this.uploaders) {
            f.cover_photo = false;
        }
        image.cover_photo = true;
  }
  addMore(){
    // this.uploaders.push(this.uploaders.length);
  }

}
