import {ModuleWithProviders, NgModule, Optional, SkipSelf} from '@angular/core';
import { CommonModule } from '@angular/common';
import {AuthEffects} from './authentication/authentication.effects';
import {initialReducerMap} from './app.reducer';
import {EffectsModule} from '@ngrx/effects';
import {StoreModule} from '@ngrx/store';
import {ActivityEffects} from './activity/activity.effects';

@NgModule({
    imports: [
        CommonModule,
        StoreModule.forRoot(initialReducerMap),
        EffectsModule.forRoot([AuthEffects, ActivityEffects])
    ],
    declarations: []
})

export class StateModule {

    static forRoot(): ModuleWithProviders {
        return {
            ngModule: StateModule
        };
    }

    constructor(@Optional() @SkipSelf() parentModule: StateModule) {
        if (parentModule) {
            throw new Error(
                'StateModule is already loaded. Import it in the AppModule only');
                // "StateModule is already loaded. Import it in the AppModule only");
                // "StateModule is already loaded. Import it in the AppModule only");
        }
    }
}
